import { createStore, applyMiddleware, compose } from 'redux';
import thunkMiddleware from 'redux-thunk';
import reducers from '../reducers/reducers';
var middleWare = applyMiddleware(thunkMiddleware);
var composedEnhancers = compose(middleWare);
// const store = storeWithMiddleWare(StocksOrdersReducer, undefined, );
var store = createStore(reducers, undefined, composedEnhancers);
export default store;
