import * as React from 'react';
import * as ReactDOM from 'react-dom';
import App from './components/app';
import { Provider, connect } from 'react-redux';
import store from '../src/store/store';
import { compose } from 'redux';
import { GetStocks } from '../src/actions/stocksOrdersActions';
var mapStateToProps = function (state) {
    return {
        stocks: state.stocks,
        orders: state.orders
    };
};
var mapDispatchToProps = function (dispatch) {
    return {
        getStocks: function () { return dispatch(GetStocks()); }
    };
};
var ContainedApp = compose(connect(mapStateToProps, mapDispatchToProps))(App);
ReactDOM.render(React.createElement(Provider, { store: store },
    React.createElement(ContainedApp, null)), document.getElementById("app"));
